.swiper {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}

.swiper-slide {
  position: relative; /* 텍스트 컨테이너를 위한 위치 지정 */
  background-position: center;
  background-size: cover;
  width: 300px;
  height: 300px;
  background-color: gray;
  border-radius: 20px; /* 라운드 처리 */
  display: flex; /* 텍스트를 중앙에 배치하기 위한 설정 */
  align-items: center; /* 세로 축 중앙 정렬 */
  justify-content: center; /* 가로 축 중앙 정렬 */
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%; /* 이미지가 슬라이드의 크기를 채우도록 */
  border-radius: 20px; /* 이미지에도 라운드 처리 */
  object-fit: cover; /* 이미지 비율을 유지하면서 컨테이너에 맞춤 */
}

/* 추가된 스타일: 텍스트 컨테이너 */
.swiper-slide .text-container {
  position: absolute;
  width: 90%; /* 혹은 원하는 너비 */
  text-align: center;
  color: white; /* 텍스트 색상 */
  font-size: 16px; /* 텍스트 크기 */
  background-color: rgba(0, 0, 0, 0.5); /* 배경 투명도 */
  padding: 10px;
  border-radius: 15px; /* 컨테이너 라운드 처리 */
}

.progress-ring {
  position: absolute;
  right: 20px; /* 우측 하단 모서리에서 오른쪽으로 10px 떨어진 위치 */
  bottom: 50px; /* 우측 하단 모서리에서 아래로 10px 떨어진 위치 */
  z-index: 100; /* 다른 swiper 내용 위에 나타나도록 z-index 설정 */
}

.progress-ring__circle {
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
  animation: progress-ring-dash 2s linear infinite;
}

@keyframes progress-ring-dash {
  0% {
    stroke-dasharray: 138.2, 138.2;
    stroke-dashoffset: -138.2;
  }
  100% {
    stroke-dasharray: 138.2, 138.2;
    stroke-dashoffset: 0;
  }
}
